import "./index.scss"
import React, { Component } from "react"
import Head from "~/components/head"
import bgImg from "@/assets/images/mine.gif"
import { List, Card, } from 'antd-mobile';
import UploadImg from "../../components/uploadImg"
import { axios } from "&"
// import { getMobile } from "../../actions";
import { connect } from "react-redux"
var tele = ""

@connect(
    state => (
        {
            token: state.getIn(['user', 'token']),
            mobile: state.getIn(['user', 'mobile']),
        }
    )
)
class Mine extends Component {

    constructor(props) {
        super(props);
        this.state = {
            token: !!sessionStorage.token,
            mobile: !!sessionStorage.mobile
        }
    }

    componentWillMount() {
        if (sessionStorage.mobile) {
            tele = sessionStorage.mobile.toString();
            tele = tele.split("")
            tele.splice(3, 4, "****").join("")
            console.log(tele)
        } else {
            this.props.history.push("/login");

        }


    }

    componentDidMount() {
        axios.post("/react/getMobile")
            .then(res => {
                this.setState({
                    mobile: res.data.result

                })
            })
    }
    gotoLogin() {
        sessionStorage.mobile = ""
        sessionStorage.token = ""
        this.props.history.push("/login");


    }
    gotomylove = () => {
        this.props.history.push("/mylove");

    }
    render() {
        const Item = List.Item;
        const Brief = Item.Brief;
        const {
            token,
            mobile
        } = this.props
        return (

            <div>
                <Head title="个人中心"></Head>
                <div className="top">
                    <img className="img2" src={bgImg} alt="" />
                    <div className="uploadImg"> <UploadImg></UploadImg> </div>

                </div>

                <Card>
                    <Card.Header
                        title={tele}
                        thumb="https://gw.alipayobjects.com/zos/rmsportal/MRhHctKOineMbKAZslML.jpg"
                        extra={<span>欢迎您使用本产品</span>}
                    />
                    <Card.Body>
                        <div>为了更美好的明天</div>
                    </Card.Body>
                </Card>
                <div className="lists" onClick={this.gotomylove}><span className="iconfont icon-xihuan xihuan"></span>   进入我的喜欢</div>
                <div className="lists"><span className="iconfont icon-gouwuche xihuan"></span>   看看我的购物车</div>
                <div className="lists"><span className="iconfont icon-youqu xihuan"></span>   我的兴趣</div>

                <button className="mineBtn" onClick={this.gotoLogin}>退出登录</button>
            </div>

        )
    }
}
export default Mine